.App {
  text-align: center;
  /* background: red; */
  height:100%;
}
h1{
  margin-bottom: 1rem;
}
.text-input{
  width: 20rem;
  height:2.4rem;
  line-height: 2.4rem;
  border: 1px solid #409eff;
  font-size: 16px;
  padding-left:.4rem;
  border-radius: .2rem;
  -webkit-border-radius: .2rem;
  -moz-border-radius: .2rem;
  -ms-border-radius: .2rem;
  -o-border-radius: .2rem;
  outline: none;
}
.add-button{
  width:6rem;
  height:3rem;
  border:none;
  outline: none;
  background: #409eff;
  border-radius: .5rem;
  -webkit-border-radius: .5rem;
  -moz-border-radius: .5rem;
  -ms-border-radius: .5rem;
  -o-border-radius: .5rem;
  margin-left:1rem;
}

.list{
  margin: 2rem;
  font-size: 16px;
 
  /* border:1px solid silver; */
  border-radius: .5rem;
  -webkit-border-radius: .5rem;
  -moz-border-radius: .5rem;
  -ms-border-radius: .5rem;
  -o-border-radius: .5rem;
}

.list .item{
  padding-bottom:.6rem;
  padding-top: .6rem;
  padding-left: 1rem;
  padding-right: 1rem;
  border: 1px solid silver;
  border-bottom: none;
}
.list .item:last-child{
  border-bottom: 1px solid silver;
}
.list .item::after{
  display: block;
  content: '';
  clear: both;
}
.text{
  float: left;
  width: 60%;
  overflow:hidden;
  text-overflow:ellipsis;
  white-space:nowrap;
}
.delete,
.edit{
  float: right;
  width:5rem;
  height:2rem;
  line-height: 2rem;
  text-align: center;
  border:none;
  outline: none;
  background: #409eff;
  border-radius: .2rem;
  -webkit-border-radius: .2rem;
  -moz-border-radius: .2rem;
  -ms-border-radius: .2rem;
  -o-border-radius: .2rem;
  margin-left:1rem;
  font-size: 14px;
  cursor: pointer;
}
.delete{
  background: #f56c6c;
}
@media screen and (max-width: 480px) {
  .delete,
  .edit{
    width:15%;
  }
}